.App-logo {
  height        : 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height      : 100vh;
  display         : flex;
  flex-direction  : column;
  align-items     : center;
  justify-content : center;
  font-size       : calc(10px + 2vmin);
  color           : white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.block {
  border-radius   : 5px;
  padding         : 1rem;
  background-color: #efefef;
  border-color    : gainsboro;
  border-style    : solid;
  border-width    : 1px;
  margin-bottom   : 1rem;
  max-width       : 80%;
  color           : #243946;
}

.label {
  font-weight : bolder;
  margin-right: 1rem;
}

.infoBlock {
  font-size    : 0.8rem;
  color        : gray;
  margin-top   : 2rem;
  margin-bottom: 2rem;
}